<script>
// import TableDeviceItem from './Table-device-item.vue';
import { actionTypes } from '@/store/modules/logs';
import { mapState } from 'vuex';
import TableLogiItem from './TableLogiItem.vue';

export default {
  name: 'AppTableLogi',
  data() {
    return {
      pageNumber: 0,
      item_device: '',
      pageOfItems: [],
      filterTime: '',
      typePackage: '',
    };
  },
  props: {
    size: {
      type: Number,
      required: false,
      default: 5,
    },
  },
  components: { TableLogiItem },
  computed: {
    ...mapState({
      isLoading: (state) => state.devices.isLoading,
      logs: (state) => state.logs.logs,
      filterTimeLogs: (state) => state.logs.filterTimeLogs,
    }),
    paginatedData() {
      const start = this.pageNumber * this.size,
        end = start + this.size;
      // return this.logs.slice(start, end);
      if (this.filterTime) {
        return this.filterTimeLogs.slice(start, end);
      } else return this.logs.slice(start, end);
    },
    pageCount() {
      let l = this.logs.length,
        s = this.size;
      return Math.ceil(l / s);
    },
    filterTimeReact() {
      return this.filterTime;
    },
  },
  watch: {
    filterTimeReact() {
      if (this.filterTimeReact !== '') {
        this.$store
          .dispatch(actionTypes.getFilterTimeLogs, this.filterTimeReact)
          .then(() => {})
          .catch((er) => {
            console.log('–û—à–∏–±–∫–∞', er);
          });
      }
      //   let path = this.searchReact;
      //   if (path === '') {
      //   } else {
