      //       .dispatch(actionTypes.getWeapon, path)
      //       .then((response) => {})
      //       .catch((er) => {
      //         console.log('–û—à–∏–±–∫–∞', er);
      //       });
      //   }
    },
  },
  methods: {
    nextPage() {
      this.pageNumber++;
    },
    prevPage() {
      this.pageNumber--;
    },
  },

  mounted() {
    this.$store
      .dispatch(actionTypes.getLogs)
      .then(() => {})
      .catch((er) => {
